import { Button, DatePicker, Form, Space, Table, Tag } from "antd";
import React from "react";
import { Link } from "react-router-dom";
import { useExpenseList } from "../../../talons/Expense/useExpenseList";
import { iBankAccount } from "../../../types/bankAccount.types";
import { iStaff } from "../../../types/user.types";
import mergeClasses from "../../../utils/mergeClasses";

import defaultClasses from "./expenseList.module.css";

interface Props {
    classes?: object;
}

const ExpenseList = ({ classes: propsClasses }: Props) => {
    const classes = mergeClasses(defaultClasses, propsClasses);

    const {
        data,
        pageSize,
        loading,
        totalNumber,

        onDelete,
        handleSearch,
        handleChangeTable,
    } = useExpenseList();

    const columns = [
        {
            title: "ID",
            dataIndex: "id",
            key: "id",
        },
        {
            title: "Tn",
            dataIndex: "name",
            key: "name",
            width: 300,
            sorter: (a: any, b: any) => NaN,
        },
        {
            title: "M t",
            dataIndex: "description",
            key: "description",
            width: 300,
            render: (record: string) => {
                if (record.length > 50) return <p>{record.slice(0, 50)}...</p>;
                return <p>{record}</p>;
            },
        },
        {
            title: "Ngy to",
            dataIndex: "date",
            key: "date",
            width: 300,
            render: (record: Date) => {
                return <p>{new Date(record).toLocaleDateString()}</p>;
            },
        },
        {
            title: "Loi",
            dataIndex: "type",
            key: "type",
            width: 150,
            render: (type: any) => {
                let color = "volcano";

                switch (type) {
                    case "Mua sm":
                        color = "geekblue";
                        break;
                    case "Trang thit b":
                        color = "green";
                        break;
                    case "Tr lng":
                        color = "volcano";
                        break;
                }

                return (
                    <span>
                        <Tag color={color}>{type}</Tag>
                    </span>
                );
            },
        },
        {
            title: "Tng tin",
            dataIndex: "total",
            key: "total",
            width: 150,
            sorter: (a: any, b: any) => NaN,
        },

        {
            title: "S ti khon ngn hng",
            dataIndex: "bankAccount",
            key: "bankAccount",
            width: 300,
            render: (record: iBankAccount) => {
                return <p>{record.accountNumber}</p>;
            },
        },
        {
            title: "Nhn vin ",
            dataIndex: "staff",
            key: "staff",
            width: 300,
            render: (record: iStaff) => {
                return (
                    <p>
                        {record.id} - {record.name}
                    </p>
                );
            },
        },

        {
            title: "Thao tc",
            key: "action",
            render: (text: any, record: any) => (
                <Space size="middle">
                    <Link to={`/expense/view/${record.id}`}>
                        <Button type="primary">Xem</Button>
                    </Link>
                    <Link to={`/expense/edit/${record.id}`}>
                        <Button type="primary">Sa</Button>
                    </Link>
                    <Button
                        type="primary"
                        danger
                        onClick={() => onDelete(record.id)}
                    >
                        Xa
                    </Button>
                </Space>
            ),
            width: 300,
        },
    ];
    return (
        <div className={classes.root}>
            <Link to="/expense/add">
                <Button type="primary">Thm mi</Button>
            </Link>

            <div className={classes.header}>
                <Form layout="inline" onFinish={handleSearch}>
                    <Form.Item label="Tm kim theo ngy" name="createdAt">
                        <DatePicker />
                    </Form.Item>
                    <Form.Item>
                        <Button type="primary" htmlType="submit">
                            Tm kim
                        </Button>
                    </Form.Item>
                </Form>
            </div>
            <Table
                columns={columns}
                dataSource={data}
                pagination={{
                    pageSize: pageSize,
                    total: totalNumber,
                    pageSizeOptions: ["10", "20", "30"],
                }}
                onChange={handleChangeTable}
                scroll={{ x: "500px" }}
                loading={loading}
            />
        </div>
    );
};

export default ExpenseList;
